html{
  font-size: 62.5%;
}

body {
  margin: 0px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.head{
  width: auto;
  height: 8rem;
  padding-top: 5rem;
  background-color: beige;
  display: flex;
  align-items: center;
  justify-content: center;
  color:rgb(163, 163, 119);
  text-shadow: 0.5rem 1.5rem 3rem rgb(156, 156, 142);
  font-size: 2rem;
}
.search{
  width: auto;
  height: 10rem;
  
  background-color: beige;
  display: flex;
  align-items: center;
  justify-content: center;
}
.input{
  width: 50rem;
  height: 4rem;
  font-size: 2rem;
  box-shadow: 0.5rem 1.5rem 3rem rgb(189, 189, 174);
  border: none;
  background-color: rgb(252, 252, 239);
  border-radius: 0.5rem;
  margin: 10rem;
  outline: none;
}
.button{
  width: 20rem;
  height: 4rem;
  font-size: 2rem;
  background-color: rgb(224, 224, 196);
  box-shadow: 0.5rem 1.5rem 3rem rgb(189, 189, 174);
  border: none;
  border-radius: 0.5rem;
  outline: none;  
}
.bg_color{
  background-color: beige;
}
h2{
  width: auto;
  height: 5rem;
  padding-top: 1rem;
  background-color: beige;
  display: flex;
  align-items: center;
  justify-content: center;
  color:rgb(163, 163, 119);
  text-shadow: 0.5rem 1.5rem 3.rem rgb(156, 156, 142);
  font-size: 2rem;
}
.recipe{
  width: auto;
  height: auto;
  background-color: rgb(253, 253, 241);
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 2rem;
}
.button1{
  width: 15rem;
  height: 4rem;
  margin: 1rem;
  background-color: rgb(224, 224, 196);
  box-shadow: 0.5rem 1.5rem 3.0rem rgb(189, 189, 174);
  border: none;
  border-radius: 0.5rem;
  outline: none;
}
a{
  color:black;
  outline: none;
  text-decoration: none;
}
.box{
  width: 40rem;
  height: auto;
  border: 0.2rem black;
  background-color: rgb(215, 215, 223);
  margin: 2rem;
  display: grid;
  justify-items: center;
  align-items: center;
  box-shadow: 0.5rem 1.5rem 3.0rem rgb(189, 189, 174);
  border-radius: 1.0rem;
}
li{
  font-size: 1.2rem;
}
.button:hover{
  background-color: rgb(189, 189, 126);
}
.button1:hover{
  background-color: rgb(131, 131, 83);
}

@media screen and (max-width: 700px){
  html{
    font-size: 50%;
  }
  h1{
    font-size: 3rem;
  }
  
  .search{
    width: auto;
    height: auto;
    display: block;
    text-align: center;
  }
  .input{
    margin: 5rem;
    width: 70%;
  }
  .button{
    margin-bottom: 5rem;
    width: 40%;
  }
  
}










































